@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@400;500;600&family=Montserrat:wght@400;500;600;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}



.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.site-footer{background:darkgrey;margin:10px;border-radius:5px}.site-footer h2{font-family:"Fira Code", monospace;margin:0}.site-footer p{font-family:"Montserrat", sans-serif}@media screen and (min-width: 900px){.site-footer p{font-size:16px}}.footer-grid{display:flex;justify-content:space-evenly}.footer-grid .blurb{flex:9 1}.footer-grid .links-container{flex:4 1}.footer-grid .footer-links{padding-left:0;margin:0;-webkit-columns:100px 2;columns:100px 2}.footer-grid .footer-links li{list-style:none}.footer-grid .footer-links a{font-family:"Montserrat", sans-serif;text-decoration:underline}

header{display:flex;flex-direction:row;margin:10px;padding:10px;border-radius:5px;background:#66bb6a;font-family:"Fira Code", monospace}header h1{font-weight:600;margin:0 0 0 5px}header .headerLinks{display:flex;flex-direction:row;align-content:center;margin-left:auto}header .headerLinks h2{margin:0;border-radius:5px;line-height:normal}header .headerLinks h2:hover{background:#ddd}header .headerLinks h2.active{background-color:#4CAF50;color:white}header .headerLinks h2:last-of-type{margin-right:5px}header .icon{display:none}@media screen and (max-width: 660px){header h2{display:none}header .icon{margin-left:auto;display:block;position:absolute;top:19px;right:24px;font-size:30px;border:none}header .headerLinks{visibility:hidden;transition:visibility 0s, opacity 0.5s linear}}@media screen and (max-width: 660px){header.responsive{display:block}header.responsive .headerLinks{visibility:visible;display:flex;flex-direction:column;align-items:flex-start}header.responsive h1{text-align:left}header.responsive h2{display:block;margin-left:5px}}


#project-grid-parent{margin:10px;background:#FFD43B;border-radius:5px}#project-grid-parent>#project-header{background:grey;border-radius:5px 5px 0 0;margin-bottom:2px;padding:5px}#project-grid-parent>#project-header h2{margin-bottom:0;font-family:"Fira Code", monospace;font-size:24px}#project-grid-parent>#project-header p{margin:0 0 3px;font-family:"Montserrat", sans-serif;font-size:18px}@media (max-width: 430px){#project-grid .GridSizer,#project-grid .OneWide,#project-grid .FourByOne .Medium{width:100%}}@media (min-width: 431px) and (max-width: 659px){#project-grid .GridSizer,#project-grid .OneWide .Medium{width:49%}#project-grid .FourByOne{width:100%}}@media (min-width: 660px) and (max-width: 899px){#project-grid .GridSizer,#project-grid .OneWide .Medium{width:33%}#project-grid .FourByOne{width:99%}}@media (min-width: 900px) and (max-width: 1299px){#project-grid .GridSizer,#project-grid .OneWide{width:24.9%}#project-grid .Medium{width:49.8%}#project-grid .FourByOne{width:74.6%}}@media (min-width: 1300px) and (max-width: 1559px){#project-grid .GridSizer,#project-grid .OneWide{max-width:300px;width:19.9%}#project-grid .Medium{max-width:600px;width:39.8%}#project-grid .FourByOne{width:59.5%;max-width:900px}}@media (min-width: 1560px){#project-grid .GridSizer,#project-grid .OneWide{width:300px}#project-grid .Medium{width:600px}#project-grid .FourByOne{width:900px}}.GridElement{padding:5px}.GridElement .GridElementInternal{padding:9px;border-radius:5px;background:lightcyan;box-shadow:0 1px 2px rgba(0,0,0,0.15);transition:box-shadow 0.3s ease-in-out}.GridElement .GridElementInternal:hover{box-shadow:0 2px 5px rgba(0,0,0,0.3)}.GridElement h2{font-family:"Fira Code", monospace;font-weight:400;margin-bottom:0px;line-height:normal}.GridElement h3{line-height:normal;color:darkgrey;margin-bottom:3px}.GridElement p{font-family:"Montserrat", sans-serif;font-size:16px;color:black;margin-bottom:0}.OneWide{display:flex;flex-direction:column;width:available}.OneWide img{width:100%;margin-bottom:5px}.Medium img{width:100%;margin-bottom:5px}@media (max-width: 899px){.Medium{display:flex;flex-direction:column;width:available}.Medium img{width:100%;margin-bottom:5px}}@media (min-width: 900px){.Medium .image{float:left;width:50%;-webkit-shape-outside:inset(0);shape-outside:inset(0)}.Medium .image img{margin-bottom:0}.Medium .description{display:inline}.Medium .description p{text-align:justify}.Medium .description h2 h3{display:inline}}@media (min-width: 1561px){.Medium .image{width:40%}}.FourByOne .GridElementInternal{display:flex;align-items:center}.FourByOne .GridElementInternal .image{flex:1 1}.FourByOne .GridElementInternal .image img{width:100%;max-width:100%}.FourByOne .GridElementInternal .description{min-width:20ch;width:30ch}@media (min-width: 661px){.FourByOne .GridElementInternal{flex-direction:row}}@media (max-width: 660px){.FourByOne .GridElementInternal{flex-direction:column}.FourByOne .GridElementInternal .description{width:95%}}.LinkIcon{position:absolute;right:12px;top:12px;font-size:24px;color:black;background:rgba(224,255,255,0.7);border-radius:5px}

#about-me-parent{margin-left:10px;margin-right:10px;display:flex;flex-direction:row;justify-content:center}#about-me-parent b{font-weight:500}#about-me-parent .about-me-card{background:#61dafb;border-radius:5px;margin:0 5px 0;padding:10px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}#about-me-parent #about-me{background:#61dafb;border-radius:5px;padding:15px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}#about-me-parent #about-me #info-parent{display:flex;flex-direction:row}#about-me-parent #about-me #info-parent #specific-info{flex:1 1}#about-me-parent #about-me #info-parent #fun-info{flex:1 1}@media screen and (max-width: 660px){#about-me-parent #about-me #info-parent{flex-direction:column}}#about-me-parent #about-me h1{font-family:"Fira Code", monospace;font-weight:500}#about-me-parent #about-me h2{font-family:"Fira Code", monospace;line-height:normal;margin-bottom:0;font-weight:400;text-align:left}#about-me-parent #about-me p{font-family:"Montserrat", sans-serif;font-size:16px;margin:0}#about-me-parent #about-me ul{text-align:left;padding-left:40px;margin-bottom:0}#about-me-parent #about-me li{font-family:"Montserrat", sans-serif;font-size:18px;list-style-type:symbols("+");list-style-position:inside}button{background:none !important;border:none;padding:0 !important;color:#1890ff;cursor:pointer}

